import React, { useState, useEffect } from "react";
import {
  CCard,
  CCardBody,
  CCol,
  CRow,
  CCardTitle,
  CCardSubtitle,
  CCardText,
} from "@coreui/react";
import axios from "axios";
import { useParams } from "react-router-dom";
// import { BsCurrencyRupee } from 'react-icons/ee';

const ChatHistory = () => {
  const { id } = useParams();

  const token =
    typeof window !== "undefined" ? localStorage.getItem("status") : null;
  // const Id =
  //   typeof window !== "undefined" ? localStorage.getItem("admin_id") : null;

  const [ChatHistory, setChatHistory] = useState([]);
  console.log("history", id);

  useEffect(() => {
    axios
      .get("getAllOrders/" + id, {
        headers: {
          Accept: "application/json",
          Authorization: "Bearer " + token,
        },
      })
      .then((res) => {
        console.log("history", res.data);
        res.data.status == 200 ?   setChatHistory(res.data.data):  setChatHistory([])
        // setChatHistory(res.data.data);
      });
  }, [token]);

  return (
    <>
{ChatHistory.length == 0 ? (
        <div className="text-center">
          <span style={{ color: "red" }}>No Call/Chat Details Available</span>
        </div>
      ) : (
      <CRow className="mb-2 px-2">
        {ChatHistory.map((value, index) => {
          return (
            <CCol key={index} sm={12} md={6} lg={4}>
              <CCard className="shadow mb-3 bg-body rounded">
                <CCardBody>
                  
                {/* {value?.user_name == "" && value?.call_type == "" && value?.date == "" && value?.duration == "" 
                && value?.call_rate == "" && value?.amount == "" ?  (
                        <div className="text-center">
                          <span style={{ color: "red" }}>
                            No Call/Chat Details Available
                          </span>
                        </div>

                      ) :

                      (
                        <>
                
                        {" "} */}
                      <CCol md={12}  className="d-flex ">
                      <CCol sm={1}> </CCol>{" "}
                        <CCol sm={6}>
                          <span style={{ marginRight: "15px" }}>
                            <span class="fw-semibold">Lawyer Name :</span>
                          </span>{" "}
                        </CCol>{" "}
                        <CCol sm={7}>
                          <span className="text-capital">{value?.lawyer_name}</span>
                        </CCol>
                      </CCol>

                     

                      <CCol md={12} className="d-flex">
                      <CCol sm={1}> </CCol>{" "}
                        <CCol sm={6}>
                          <span style={{ marginRight: "15px" }}>
                            <span class="fw-semibold">Order Type: </span>
                          </span>{" "}
                        </CCol>{" "}
                        <CCol sm={7}>
                          <span className="text-capital">{value?.call_type}</span>
                        </CCol>
                      </CCol>

                      <CCol md={12} className="d-flex ">
                      <CCol sm={1}> </CCol>{" "}
                        <CCol sm={6}>
                          <span style={{ marginRight: "15px" }}>
                            <span class="fw-semibold">Date: </span>
                          </span>{" "}
                        </CCol>{" "}
                        <CCol sm={7}>
                          <span className="text-capital">{value?.date}</span>
                        </CCol>
                      </CCol>
                      <CCol md={12} className="d-flex ">
                      <CCol sm={1}> </CCol>{" "}
                        <CCol sm={6}>
                          <span style={{ marginRight: "15px" }}>
                            <span class="fw-semibold">Duration: </span>
                          </span>{" "}
                        </CCol>{" "}
                        <CCol sm={7}>
                          <span className="text-capital">{value?.duration}</span>
                        </CCol>
                      </CCol>

                      <CCol md={12} className="d-flex ">
                      <CCol sm={1}> </CCol>{" "}
                        <CCol sm={6}>
                          <span style={{ marginRight: "15px" }}>
                            <span class="fw-semibold">Price/min: </span>
                          </span>{" "}
                        </CCol>{" "}
                        <CCol sm={7} >
                          <span className="text-capital">{value?.call_rate}</span>
                        </CCol>
                      </CCol>
                      
                      <CCol md={12} className="d-flex ">
                      <CCol sm={1}> </CCol>{" "}
                        <CCol sm={6}>
                          <span style={{ marginRight: "15px" }}>
                            <span class="fw-semibold">Total Amount:</span>
                          </span>{" "}
                        </CCol>{" "}
                        <CCol sm={7}>
                          <span className="text-capital">{value?.amount}</span>
                        </CCol>
                      </CCol>
                      
                      

                      
                  
                  
                  {/* </>
                      )} */}
                </CCardBody>
              </CCard>
            </CCol>
          );
        })}
      </CRow>
      )}
    </>
  );
};

export default ChatHistory;
